@import "@/scss/main.scss";

























































  .top-menu {
    flex-basis: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: $smDesktopWidth) {
      flex-basis: 50%;
    }
    @media screen and (max-width: $phoneWidth) {
      justify-content: flex-end;
    }
    #menu-toggle {
      width: 40px;
      display: none;
      @media screen and (max-width: $phoneWidth) {
        display: block;
      }
      div:first-child {
        margin-top: 0;
      }
      div {
        width: 100%;
        height: 4px;
        background-color: $hover-color;
        margin-top: 5px;
      }
    }
    .menu-toggle_fix {
      position: fixed;
      top: 100px;
      left: 0;
      background-color: rgba($success-color, 0.9);
      width: 100vw;
      z-index: 5;
      color: $default-color;
      div, li, button {
        transform-origin: top;
        transition: .3s ease-in-out;
      }
      div {
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        height: 0;
        ul {
          display: none;
        }
        li {
          height: 0;
          opacity: 0;
        }
        li a {
          font-size: 20px;
          color: inherit;
        }
        button {
          margin: 10% auto;
          opacity: 0;
        }
        .close {
          width: 20px;
          height: 0;
          position: absolute;
          right: 5%;
          top: 5%;
        }
        .close:before, .close:after {
          content: "";
          width: 3px;
          display: block;
          height: 100%;
          background-color: $default-color;
          position: absolute;
          top: 0;
          left: 8px;
        }
        .close:before {
          transform: rotate(45deg);
        }
        .close:after {
          transform: rotate(-45deg);
        }
      }
      div.active {
        padding-top: 10%;
        min-height: 500px;
        height: 50vh;
        ul {
          display: inline-block;
        }
        button {
          opacity: 1;
        }
        .close {
          height: 20px;
        }
      }
      div.active li {
        padding: 10px;
        height: auto;
        opacity: 1;
        transform-origin: top;
        transition: .5s ease-in-out;
      }
    }
  }

.top-menu-items {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    @media screen and (max-width: $phoneWidth) {
      display: none;
    }
    li {
      a {
        font-size: rem(18px);
        color: rgba($success-color, 0.5);
        letter-spacing: rem(0, 5);
        line-height: rem(36px);
        transition: .2s ease;
      }
    }

    a:hover {
      color: $hover-color;
      font-weight: $fontWeightBlack;
      line-height: rem(36px);
      transform-origin: center;
    }
  }

