@import "@/scss/main.scss";

















.btn {
  font-family: inherit;
  color: $default-color;
  line-height: normal;
  width: max-content;
}
.big:hover, .small:hover, .medium:hover{
  background-color: transparent;
  color: $hover-color;
  cursor: pointer;
}

.small {
  background-color: $hover-color;
  padding: 15px 45px;
  box-shadow: -8px 0 54px rgba(#ff0005, 0.19);
  font-weight: $fontWeightLight;
  font-size: rem(20px);
  border-radius: 40px;
  border: 2px solid $hover-color;
  @media  screen and (max-width: $tableWidth) {
    padding: 10px 40px;
  }
}
.medium {
  padding: 30px;
  background-color: $hover-color;
  border: 2px solid $hover-color;
  box-shadow: -8px 0 24px rgba(#ff0005, 0.19);
  @media screen and (max-width: $smDesktopWidth) {
    padding: 20px;
  }
}
.medium,.medium-blue {
  font-style: $fontStyle;
  font-weight: 500;
  font-size: rem(18px);
  border-radius: 37px;
}
.medium-blue {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#5fcef5+0,83e4fc+100 */
  background: #5fcef5; /* Old browsers */
  background: -moz-linear-gradient(left, #5fcef5 0%, #83e4fc 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #5fcef5 0%,#83e4fc 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #5fcef5 0%,#83e4fc 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5fcef5', endColorstr='#83e4fc',GradientType=1 ); /* IE6-9 */
  box-shadow: -8px 0 24px rgba($link-color, 0.19);
  padding: 30px;
  @media screen and (max-width: $smDesktopWidth) {
    padding: 20px;
  }
}
.medium-blue:hover {
  padding: 28px;
  background: transparent;
  color: $link-color;
  cursor: pointer;
  border: 2px solid $link-color;
  @media screen and (max-width: $smDesktopWidth) {
    padding: 18px;
  }
}

.big {
  background-color: $hover-color;
  padding: 30px 85px;
  box-shadow: -12px 10px 24px rgba($hover-color, 0.5);
  font-weight: $fontWeightLight;
  font-size: rem(20px);
  border-radius: 40px;
  border: 2px solid $hover-color;
  @media (min-width: $tableWidth) and (max-width: $desktopWidth) {
    padding: 20px 40px;
  }
  @media  screen and (max-width: $tableWidth) {
    padding: 10px 40px;
  }
}
