@import "@/scss/main.scss";













































































































































































































.cont-videos {
  min-height: 760px;
  max-height: 100vmin;
  @media (min-width: $smDesktopWidth) and (max-width: $desktopWidth) {
    min-height: 560px;
  }
  @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
    min-height: 360px;
  }
  @media screen and (max-width: $tableWidth) {
    min-height: 300px;
  }

  .videos {
    flex-basis: 36%;
    margin: 60px 0 0 120px;
    @media screen and (max-width: $desktopWidth) {
      margin: 30px 0 0 120px;
      flex-basis: 50%;
    }
    @media screen and (max-width: $smDesktopWidth) {
      margin: 50px 0;
      flex-basis: 60%;
    }
    @media screen and (max-width: $phoneWidth) {
      flex-basis: 100%;
    }

    p {
      padding: 60px 0;
      @media screen and (max-width: $desktopWidth) {
        padding: 20px 0;
      }
    }

    .left {
      margin-left: 25px;
    }
  }
}

.why-choose {
  text-align: center;

  h2.title2, p {
    max-width: 660px;
    margin: 0 auto;
  }

  p {
    padding-top: 35px;
  }

  &_items {
    justify-content: space-between;
    padding: 90px 70px 0 70px;
    @media screen and (max-width: $desktopWidth) {
      margin-top: 50px;
      padding: 0;
    }
    @media screen and (max-width: $phoneWidth) {
      justify-content: center;
    }
  }
}

.what {
  background: url("../assets/section-what.png") no-repeat top 130px right;
  background-size: 100%;

  &-cont {
    justify-content: flex-end;
    flex-wrap: wrap;

    &_img {
      img {
        width: 100%;
        margin-left: -20px;
        @media screen and (max-width: $tableWidth) {
          margin-left: -40px;
        }
        @media screen and (max-width: $phoneWidth) {
          margin-left: 0;
        }
      }
    }

    &_desc {
      @media screen and (max-width: $desktopWidth) {
        padding: 0;
      }

      h2.title2 {
        margin-bottom: 0;
      }

      &_check {
        padding: 50px 0 0 70px;
        @media screen and (max-width: $smDesktopWidth) {
          padding: 20px 0 0 0;
        }

        h5.title5 {
          opacity: .75;
        }

        h5.title5:before {
          content: "";
          background: url("../assets/cheklist.png") no-repeat;
          background-size: 100%;
          width: 31px;
          height: 24px;
          position: absolute;
          margin-left: -50px;
          @media screen and (max-width: $phoneWidth) {
            display: none;
          }
        }
      }

      &_check:first-child {
        padding-top: 0;
      }
    }
  }
}

.power {
  span {
    width: 100%;
  }

  &_desc {
    padding: 0 0 0 50px;
    @media screen and (max-width: $smDesktopWidth) {
      padding: 0;
    }
    @media screen and (max-width: $phoneWidth) {
      order: 2;
    }

    span {
      font-style: italic;
    }

    p {
      padding: 0 50px;
      max-width: 70%;
      text-indent: 20px;
      @media screen and (max-width: $desktopWidth) {
        padding: 0;
        max-width: 80%;
      }
      @media screen and (max-width: $phoneWidth) {
        max-width: 100%;
      }
    }

    a {
      padding: 65px 0 0 50px;
      @media screen and (max-width: $desktopWidth) {
        padding: 40px 0;
      }
      @media screen and (max-width: $phoneWidth) {
        padding: 20px 0 0 0;
      }
    }
  }

  &_img {
    img {
      margin-top: -50%;
      width: 100%;
      @media screen and (max-width: $tableWidth) {
        margin-top: -20%;
      }
      @media screen and (max-width: $phoneWidth) {
        margin: 0;
      }
    }
  }
}

.ways {
  background: url("../assets/section-ways.png") no-repeat top right;
  background-size: 100%;

  .flex {
    justify-content: flex-end;
  }

  &_img {
    img {
      margin-top: -30%;
      @media screen and (max-width: $smDesktopWidth) {
        margin-left: -40px;
      }
      @media (min-width: $tableWidth) and (max-width: $desktopWidth) {
        width: 90%;
      }
      @media screen and (max-width: $phoneWidth) {
        width: 100%;
        margin: 0;
      }
    }
  }

  &_desc {
    &_text {
      p {
        text-indent: 20px;
      }

      a {
        padding-top: 65px;
        @media screen and (max-width: $desktopWidth) {
          padding: 40px 0;
        }
        @media screen and (max-width: $phoneWidth) {
          padding: 20px 0 0 0;
        }
      }
    }
  }

  @media screen and (max-width: $desktopWidth) {
    padding: 0;
  }
}

.sale {
  background: url("../assets/seller.png") no-repeat bottom;
  background-size: 100%;
  @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
    background: url('../assets/seller-320.jpg') no-repeat bottom -80px center;
  }
  @media (min-width: $phoneWidth) and (max-width: $tableWidth) {
    background: url('../assets/seller-320.jpg') no-repeat bottom -130px center;
  }
  @media screen and (max-width: $phoneWidth) {
    background: url('../assets/seller-320.jpg') no-repeat bottom center;
  }

  .wrap-cont {
    align-items: center;
    justify-content: center;
  }

  &_item:first-child {
    box-shadow: -42px 0 35px rgba(#ff0005, 0.05);
    @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
      flex-basis: 32.5%;
    }
    @media screen and (max-width: $tableWidth) {
      order: 2;
    }

    .sale_item-desc {
      li:before {
        background: url("../assets/li-blue.png") no-repeat;
      }
    }
  }

  &_item:nth-child(2) {
    z-index: 2;
    @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
      flex-basis: 35%;
    }
    @media screen and (max-width: $tableWidth) {
      order: 1;
    }

    .sale_item-desc {
      box-shadow: 0 0 136px rgba(#ff0005, 0.1);

      li:before {
        background: url("../assets/li.png") no-repeat;
      }
    }
  }

  &_item:last-child {
    box-shadow: 42px 0 35px rgba(#ff0005, 0.05);
    @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
      flex-basis: 32.5%;
    }
    @media screen and (max-width: $tableWidth) {
      order: 2;
      width: 50%;
    }

    .sale_item-desc {
      li:before {
        background: url("../assets/li-blue.png") no-repeat;
      }
    }
  }

  .sale_item:first-child, .sale_item:last-child {
    @media screen and (max-width: $tableWidth) {
      margin-top: 50px;
      box-shadow: 0 0 136px rgba(255, 0, 5, 0.1);
    }
  }

  .sale_item:first-child, .sale_item:nth-child(2), .sale_item:last-child {
    @media screen and (max-width: $smDesktopWidth) {
      li {
        margin-left: 50px;
      }
    }

    li:before {
      @media screen and (max-width: $desktopWidth) {
        margin-top: -5px;
      }
      @media screen and (max-width: $smDesktopWidth) {
        background-size: 80%;
        margin-left: -50px;
      }
    }
  }

  &_item {
    border-radius: 20px;
    position: relative;
    z-index: 1;
    flex-basis: 33.33333333333333%;
    @media screen and (max-width: $tableWidth) {
      flex-basis: 100%;
    }

    &-desc {
      display: flex;
      background-color: white;
      flex-direction: column;
      justify-content: center;
      border-radius: 20px;
      align-items: center;

      h4.title4 {
        width: 100%;
        text-transform: uppercase;
        font-weight: $fontWeightBlack;
        background-color: $hover-color;
        color: $default-color;
        text-align: center;
        font-size: rem(30px);
        line-height: rem(80px);
        border-radius: 20px 20px 0 0;
      }

      &_price {
        color: $title-color;
        justify-content: center;
        flex-direction: column;
        margin: rem(50px) 0;

        h6.title6 {
          margin-bottom: 45px;
        }

        ul {
          li:first-child {
            padding: 0;
          }

          li {
            opacity: .75;
            position: relative;
            margin-left: 70px;
            padding: 30px 0 0 0;
            @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
              padding-top: 20px;
            }
          }

          li:before {
            content: "";
            position: absolute;
            height: 38px;
            width: 38px;
            margin-left: -70px;
          }
        }

        .price {
          margin-top: 90px;
          opacity: .75;
          display: flex;
          @media screen and (max-width: $smDesktopWidth) {
            margin-top: 50px;
          }

          &_unit {
            vertical-align: top;
            margin-top: -20px;
            margin-right: 15px;
            font-size: rem(48px);
            @media screen and (max-width: $smDesktopWidth) {
              margin: 0 10px 0 0;
            }
          }

          &_value {
            text-align: center;
            font-size: rem(140px);
            line-height: rem(140px);
            @media screen and (max-width: $smDesktopWidth) {
              font-size: rem(90px);
              line-height: rem(90px);
            }
          }

          &_time {
            font-size: rem(30px);
            align-self: flex-end;
          }

          &_btn {
            margin: 60px auto 0;
          }
        }

        .price + p {
          text-align: center;
          opacity: .75;
        }
      }
    }
  }
}

.what, .why-choose, .power, .ways, .sale {
  padding-top: $marginTopDesc;
  @media (min-width: $smDesktopWidth) and (max-width: $desktopWidth) {
    padding-top: 150px;
  }
  @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
    padding-top: 100px;
  }
  @media (min-width: $smPhoneWidth) and (max-width: $tableWidth) {
    padding-top: 50px;
  }
}

.what-cont_desc, .power_desc, .ways_desc_text {
  @media screen and (max-width: $tableWidth) {
    margin-top: 40px;
  }
}
