@import "@/scss/main.scss";


























header {
  padding: 50px 0 0 0;
  @media screen and (max-width: $desktopWidth) {
    padding-top: 30px;
  }

  .header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
      .btn {
        @media screen and (max-width: $phoneWidth) {
        display: none;
      }
    }

    .logo {
      flex-basis: 50%;
      @media screen and (max-width: $smDesktopWidth) {
        flex-basis: 15%;
      }
      @media screen and (max-width: $phoneWidth) {
        flex-basis: 50%;
      }
    }
  }
}
