@import "@/scss/main.scss";
































.item-choose {
  flex-basis: 240px;
  text-align: center;
  @media (min-width: $phoneWidth) and (max-width: $tableWidth) {
    flex-basis: 30%;
  }
  @media screen and (max-width: $phoneWidth) {
    margin-bottom: 40px;
  }

  img {
    width: 90%;
  }

  h3.title3 {
    margin-top: 70px;
    @media screen and (max-width: $desktopWidth) {
      margin-top: 40px;
    }
    @media screen and (max-width: $phoneWidth) {
      margin-top: 20px;
    }
  }

  p {
    line-height: rem(27px);
  }
}
.item-choose:last-child {
  margin-bottom: 0;
}
