@import "@/scss/main.scss";































































footer {
  width: 100%;
  background-color: rgba($default-color, 0.5);
  border-radius: 175px 175px 0 0;
  margin-top: 130px;
  @media (min-width: $phoneWidth) and (max-width: $smDesktopWidth) {
    border-radius: 70px 70px 0 0;
  }
  @media screen and (max-width: $phoneWidth) {
    border-radius: 30px;
  }

  .footer-menu {
    max-width: 1170px;
    width: 100%;
    justify-content: space-between;
    margin: 0 auto;
    padding: 100px 50px 30px 50px;
    @media screen and (max-width: $tableWidth) {
      padding: 50px 30px 30px 30px
    }

    &_desc {
      flex-direction: column;
      @media screen and (max-width: $phoneWidth) {
        margin-bottom: 30px;
      }
    }

    &-item {
      flex-basis: 20%;

      li {
        line-height: rem(36px);

        a {
          font-size: rem(18px);
          color: rgba($success-color, 0.5);
          letter-spacing: rem(0, 5);
          line-height: inherit;
          transition: .2s ease;
        }

        a:hover {
          color: $hover-color;
          font-weight: $fontWeightBlack;
          line-height: inherit;
          transform-origin: center;
        }
      }

      h3.title7 {
        text-transform: uppercase;
        font-weight: $fontWeightBlack;
        font-size: rem(18px);
        line-height: rem(25px);
        color: rgba($neutral-secondary, 0.7);
        min-height: 60px;
        @media screen and (max-width: $tableWidth) {
          font-size: rem(16px);
          line-height: rem(20px);
          min-height: 40px;
        }

        a {
          font-size: inherit;
          line-height: inherit;
          color: inherit;
        }

        a:hover {
          color: $hover-color;
          line-height: inherit;
        }
      }
    }

    &-item:first-child {
      ul {
        margin-bottom: 20px;
      }
    }
  }

  .footer-menu + p {
    padding-bottom: 40px;
    text-align: center;
  }
}
