@import "utils/reset";
@import "utils/variables";
@import "utils/animation";
@import "utils/mixins";
@import "utils/libs";
@import "utils/font";
html {
  font-size: $mainFontSize;
  line-height: rem(36px);
  @media screen and (max-width: $desktopWidth) {
    font-size: 16px;
    line-height: rem(30px);
  }
  @media screen and (max-width: $tableWidth) {
    font-size: 14px;
    line-height: rem(25px);
  }
body {
  h1.title1 {
    font-size: rem(48px);
    line-height: rem(60px);
    @media screen and (max-width: $tableWidth) {
      font-size: rem(40px);
      line-height: rem(40px);
    }

  }
  h1.title1, h2.title2 {
    font-weight: $fontWeightBlack;
    color: $title-color;
  }

  h2.title2 {
    font-size: rem(48px);
    line-height: rem(60px);
    margin-bottom: 80px;
    width: 50%;
    @media (min-width: $smDesktopWidth) and (max-width: $desktopWidth) {
      font-size: rem(38px);
      line-height: rem(45px);
    }
    @media (min-width: $tableWidth) and (max-width: $smDesktopWidth) {
    font-size: rem(34px);
    line-height: rem(35px);
  }
    @media screen and (max-width: $phoneWidth) {
      font-size: rem(35px);
      line-height: rem(40px);
    }
    @media screen and (max-width: $desktopWidth) {
      margin-bottom: 40px;
    }
    @media screen and (max-width: $tableWidth) {
      width: 100%;
      text-align: center;
    }
  }
  h3.title3 {
    color: $title-color;
    font-size: rem(24px);
    line-height: rem(60px);
    font-weight: $fontWeightBlack;
  }
  h6.title6 {
    color: $neutral-primary;
    font-size: rem(30px);
    line-height: rem(30px);
    font-weight: $fontWeightBlack;
  }
  h5.title5 {
    color: $title-color;
    font-size: rem(24px);
    line-height: rem(40px);
    font-weight: $danger-color;
    position: relative;
  }
  .flex {
    display: flex;
    flex-wrap: wrap;
    .flex-50 {
      flex-basis: 50%;
      align-items: center;
      @media screen and (max-width: $smDesktopWidth) {
       align-self: center;
      }
      @media screen and (max-width: $phoneWidth) {
        flex-basis: 100%;
      }
    }
  }
  .flex-padding-right {
    @media screen and (max-width: $desktopWidth) {
    padding-right: 70px;
    }
    @media screen and (max-width: $smDesktopWidth) {
      padding-right: 20px;
    }
    @media screen and (max-width: $phoneWidth) {
      padding: 0;
    }
  }
  .flex-padding-left {
    padding-left: 70px;
    @media screen and (max-width: $smDesktopWidth) {
      padding-left: 20px;
    }
  }
  a.link {
    color: $link-color ;
    display: block;
  }
  a.link:hover {
    color: $hover-color;
  }
}
  .wrap-cont {
    max-width: $desktopWidth;
    margin: 0 auto;
    @media screen and (max-width: $desktopWidth ) {
      max-width: $smDesktopWidth;
    }
    @media screen and (max-width: $smDesktopWidth) {
      max-width: $tableWidth;
    }
    @media (min-width:$phoneWidth) and (max-width: $tableWidth ) {
      max-width: $phoneWidth;
    }
    @media (min-width:$smPhoneWidth) and (max-width: $phoneWidth) {
      max-width: $smPhoneWidth ;
    }
  }
}
